.reasons {
  position: relative;
  margin-bottom: 34px;
  padding: 85px 0 134px;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("../img/waves1.svg") no-repeat;
    background-position-y: -160px;
    background-position-x: -30px;
    background-size: cover;
    z-index: -1;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 35px;
  }

  &__title {
    margin-bottom: 15px;
  }

  &__descr {
    margin-bottom: 44px;
    max-width: 71%;
  }
}
.reasons-descr2 {
  display: inline-block;
  padding-top: 15px;
}

.link-portfolio {
  position: relative;
  padding: 15px 35px;
  text-align: center;
  font-size: 20px;
  border-radius: 4px;
  border: 1.5px solid #2EECC5;
  background-color: #161617;
  box-shadow: 0 0 38px 0 #2EECC533;
  transition: background-color 0.15s ease-in-out;

  &::before {
    content: '';
    position: absolute;
    bottom: -10px;
    right: -10px;
    width: 100%;
    height: 100%;
    border: 2px solid #2EECC566;
    z-index: -10;
  }

  &:hover {
    background-color: rgb(16, 56, 47);
    transition: background-color 0.15s ease-in-out;
  }

  &:active {
    background-color: rgb(6, 44, 36);
  }
}

.cards {
  position: relative;
  padding: 45px;
  border-radius: 6px;
  overflow: hidden;
  z-index: 10;

  &__title {
    margin-bottom: 20px;
    font-size: 26px;
    line-height: 32px;
    color: $white;
  }

  &__descr {
    line-height: 20px;
    color: $grey90;
  }

  &__img {
    position: absolute;
    right: -15px;
    bottom: 0;
    width: 200px;
    height: 189px;
    z-index: -1;

    &--clock {
      right: -10px;
      bottom: -10px;
      width: 187px;
    }

    &--team {
      right: 0;
      width: 144px;
    }

    &--support {
      right: 0;
      width: 183px;
    }
  }

  &:first-child {
    grid-column: 1/3;
  }

  &:nth-child(2) {
    background: linear-gradient(139.56deg, #527791 10.06%, #325597 53.72%, #193895 88%);
  }

  &:nth-child(n+3) {
    border: 1px solid #FFFFFF33;
    box-shadow: inset 0 0 80px 0 #2C2C2D59;
    background: linear-gradient(139.56deg, #303134 10.06%, #282828 53.72%, #282828 88%);
  }
}

@media (max-width: 1920px) {
  .reasons {
    height: 150vh;
  }
}

@media (max-width: 1440px) {
  .reasons {
    padding: 55px 0 64px;
    height: 250vh;

    &::before {
      background-position-y: -190px;
      background-position-x: 0;
      background-size: 2000px;
      background-repeat: repeat-y;
      z-index: -1;
    }

    &__container {
      display: grid;
      grid-template-columns: unset;
      grid-template-rows: repeat(5, 290px);
      grid-gap: 35px;
    }

    &__title {
      text-align: center;
    }

    &__descr {
      margin-bottom: 25px;
      text-align: center;
      width: 100%;
    }
  }

  .cards {
    padding: 30px;
    &:first-child {
      display: flex;
      flex-direction: column;
      align-items: center;
      grid-column: 1;
    }
  }

  .link-portfolio {
    padding: 10px 25px;
    font-size: 14px;
  }
}

@media (max-width: 1024px) {
  .reasons {
    height: unset;

    &__container {
      padding: 0 35px;
    }

    &__main {
      padding: 0;
    }

    &__descr {
      max-width: 100%;
      font-size: 12px;
    }
  }

  .cards {

    &__title {
      margin-bottom: 5px;
      font-size: 22px;
      line-height: 27px;
    }

    &__descr {
      font-size: 12px;
      line-height: 16px;
    }

    &__img {
      right: 0;
      bottom: 0;
      width: 150px;
      height: 140px;
      object-fit: unset;

      &--team {
        right: -15px;
      }

      &--support {
        right: -10px;
      }
    }
  }
}

@media (max-width: 500px) {
  .reasons {
    &::before {
      background-position-y: -80px;
      background-position-x: -105px;
      background-size: 1000px;
      background-repeat: no-repeat;
    }
  }
}
