.tariffs {
  margin-bottom: 100px;
  padding: 100px 0 215px;

  &__title {
    margin-bottom: 40px;
    text-align: center;
  }

  &__swiper {
    position: relative;
  }

  &__nav {
    padding: 0 50px;
    position: absolute;
    top: 45%;
    display: flex;
    justify-content: space-between;
    width: 100%;
    z-index: 10;
  }

  .swiper-slide {
    opacity: 0;
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
    cursor: pointer;
  }

  .swiper-slide-active {
    opacity: 1;
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  }

  .swiper-slide-prev {
    opacity: 0.3;
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  }
  .swiper-slide-next {
    opacity: 0.3;
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  }
}

.tariffs-card {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 45px 25px 25px;
  //max-width: 370px;
  min-height: 570px;
  border: 1px solid #FFFFFF33;
  border-radius: 35px;
  background: linear-gradient(168.64deg, #313134 9.31%, #292929 61.88%, #282828 111.76%);

  &__list {
    margin: 0;
    padding: 0;
  }

  &__title {
    text-align: center;
    font-size: 32px;
    line-height: 43px;
    color: $white;
  }

  &__descr {
    text-align: center;
    color: $grey75;
  }

  &__cost {
    display: flex;
    justify-content: space-between;
    align-self: flex-start;
    width: 100%;

    .price {
      font-size: 22px;
      line-height: 34px;
      color: $white;
    }

    .period {
      color: $grey40;
    }

    .link-more {
      display: inline-flex;
      align-items: center;
      margin-right: 0;
      padding-left: 25px;
      font-size: 17px;
      max-width: 172px;

      &::before {
        top: 22px;
        right: 15px;
      }
      &::after {
        top: 28px;
        right: 15px;
      }
    }
  }
}

@media (max-width: 1440px) {
  .tariffs {
    padding: 85px 0;

    &__nav {
      padding: 0 5px;
      top: 41%;
    }
  }

  .tariffs-card {
    padding: 25px 15px 15px;
    min-height: 380px;

    &__title {
      font-size: 24px;
      line-height: 26px;
    }

    &__descr {
      font-size: 12px;
    }

    &__cost {
      .price {
        font-size: 16px;
        line-height: 24px;
      }
      .period {
        font-size: 12px;
        line-height: 12px;
      }
    }

    .link-more {
      padding: 10px 30px 10px 20px;
      font-size: 12px;
      width: unset;
      max-width: unset;

      &::before {
        top: 16px;
      }
      &::after {
        top: 21px;
      }
    }
  }
}

@media (max-width: 1024px) {
  .tariffs {

    &__container {
      padding: 0;
    }

    &__swiper {
      padding-bottom: 10px;
    }

    .swiper-wrapper {
      margin-bottom: 40px;
    }

    &__nav {
      position: relative;
      top: unset;
      justify-content: center;
    }

    &__prev {
      margin-right: 35px;
    }
  }

}
