.contacts {
  height: 20vh;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    position: relative;
    margin-bottom: 70px;
    color: $white;

    &::before {
      content: '';
      position: absolute;
      bottom: -25px;
      left: -35px;
      width: 40px;
      height: 45px;
      background: url("../img/stars.svg") no-repeat;
      background-size: 40px;
    }
    &::after {
      content: '';
      position: absolute;
      top: -15px;
      right: -35px;
      width: 40px;
      height: 45px;
      background: url("../img/stars.svg") no-repeat;
      background-size: 40px;
      transform: rotate(180deg);
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 70px;
  }

  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: $white;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;

    &::before {
      content: '';
      position: absolute;
      top: -5px;
      left: -5px;
      width: 90px;
      height: 90px;
      border-radius: 50%;
      border: 1px solid #2D2D2E;
      transition: border-color 0.2s ease-in-out;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border-radius: 50%;
      background: radial-gradient(77.86% 77.86% at 49.29% 50.71%, #2EECC5 0%, #00644F 100%);
      opacity: 0;
      transition: opacity 0.2s ease-in-out;
    }

    &:not(:last-child) {
      margin-right: 55px;
    }

    &:hover {
      transform: scale(1.4);
      transition: transform 0.2s ease-in-out;
      &::before {
        border-color: #182B28;
        transition: border-color 0.2s ease-in-out;
      }
      &::after {
        opacity: 1;
        transition: opacity 0.2s ease-in-out;
      }
    }
  }

  &__link {
    display: inline-flex;
    z-index: 10;
  }

  &__icon {
    fill: $black;
  }
}

.privacy {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.web-studio {
  display: inline-block;
  margin-bottom: 10px;
  font-size: 15px;
  color: $grey75;
}

.policy {
  font-size: 13px;
  color: $grey40;
}

@media (max-width: 1024px) {
  .contacts {
    height: unset;
  }
}


@media (max-width: 768px) {
  .contacts {

    &__title {
      margin-bottom: 30px;

      &::before {
        bottom: -15px;
        left: -25px;
        width: 30px;
        height: 35px;
        background-size: 30px;
      }

      &::after {
        bottom: -15px;
        right: -25px;
        width: 30px;
        height: 35px;
        background-size: 30px;
      }
    }

    &__container {
      padding: 0;
    }

    &__list {
      margin-bottom: 15px;
      padding: 20px 0;
      width: 100%;
      background: linear-gradient(73.49deg, #313134 -57.16%, #222224 3.42%, #292929 86.16%);
    }

    &__item {
      width: 45px;
      height: 45px;
      background: radial-gradient(#FFFFFF 2.5%, rgba(255, 255, 255, 0) 100%);

      &::before {
        width: 55px;
        height: 55px;
      }

      &:not(:last-child) {
        margin-right: 25px;
      }
    }

    &__link {
      svg {
        width: 25px;
      }
    }
  }

  .privacy {
    margin-bottom: 15px;
  }

  .web-studio {
    font-size: 9px;
  }

  .policy {
    font-size: 9px;
  }
}
