.header {
  position: relative;
  padding: 30px 0;
  z-index: 100;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }


  &__nav {
    display: flex;
    align-items: center;
  }
}

.nav {
  &__list {
    display: flex;
    margin-right: 60px;
    font-size: 20px;
    line-height: 28px;
  }

  &__item {
    position: relative;

    &::before {
      content:'';
      position: absolute;
      margin: auto;
      top: -43px;
      right: 0;
      left: 0;
      width: 0%;
      height: 8px;
      border-radius: 0 0 6px 6px;
      background-color: $header-nav;
      box-shadow: 0 0 21px 0 #2CE8C259;
      transition: width 0.2s ease-in-out;
    }

    &:hover {
      &::before {
        width: 100%;
        transition: width 0.2s ease-in-out;
      }

    }

    &:not(:last-child) {
      margin-right: 35px;
    }
  }
}

.link-order {
  text-align: center;
  width: 100%;
  max-width: 145px;
}


.btn-burger {
  width: 36px;
  height: 34px;
}

.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;

  &__line {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 4px;
    background: linear-gradient(147.14deg, #2EECC5 8.04%, #3A796D 80.38%);
    z-index: -1;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(147.14deg, #1b8771 8.04%, #21453e 80.38%);
      opacity: 0;
      transition: opacity 0.15s ease-in-out;
    }

    &:nth-child(2) {
      width: 60%;
    }
  }

  &:hover {
    .burger__line::after {
      opacity: 1;
      transition: opacity 0.15s ease-in-out;
    }
  }
}

.nav-mobile {
  display: none;
}

.close {
  position: relative;
  align-self: flex-start;
  display: none;
  width: 36px;
  height: 36px;
  z-index: 100;

  &__line {
    position: absolute;
    display: block;
    width: 100%;
    height: 3px;
    border-radius: 50px;
    background: linear-gradient(139.93deg, #2EE9C3 -1.69%, #36A48E 62.63%, #3A786C 117.78%);

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #187d64;
      opacity: 0;
      transition: opacity 0.15s ease-in-out;
    }


    &:first-child {
      transform: rotate(135deg);
    }
    &:last-child {
      transform: rotate(45deg);
    }
  }

  &:hover {
    .close__line::after {
      opacity: 1;
      transition: opacity 0.15s ease-in-out;
    }
  }
}

@media (max-width: 1440px) {
  .header {
    padding: 15px 0;

    &__container {
      flex-direction: row-reverse;
    }

    &__nav {
      flex-direction: column;
      position: absolute;
      top: -15px;
      right: 0;
      left: 0;
      padding-top: 37px;
      border-radius: 15px;
      border: 1px solid #FFFFFF33;
      background: linear-gradient(132.64deg, #161919 2.37%, #373A3A 100.31%);

      transform: translateY(-150%);
      opacity: 0;
      visibility: hidden;
      transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

      &--active {
        display: flex;
        z-index: 10000;
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
        transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
      }
    }
  }

  .nav {
    &__list {
      flex-direction: column;
      align-items: center;
      margin-right: 0;
      width: 100%;
    }

    &__item {
      margin: 0 !important;
      width: 100%;
      text-align: center;

      &::before {
        top: 0;
        height: 100%;
        border-radius: unset;
        background: linear-gradient(90.18deg, #30DBB8 -17.89%, #37A58F 24.63%, #398778 99.91%);
        z-index: -1;
      }

      &:last-child::before {
        border-radius: 0 0 14px 14px;
      }
    }

    &__link {
      display: block;
      padding: 10px 55px;
      width: 100%;
      height: 100%;
    }
  }

  .logo {
    svg {
      width: 70px;
    }
  }

  .burger {
    display: flex;
  }

  .nav-mobile {
    display: inline-block;
  }

  .link-order {
    display: none;
  }

  .close {
    display: block;
    left: 125px;
  }
}

@media (max-width: 1024px) {
  .close {
    left: 15px;
  }
}

