.portfolio {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 200px 0 260px 325px;
  width: 100%;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("../img/waves2.svg") no-repeat;
    background-position-y: -400px;
    background-position-x: -400px;
    background-size: 2500px;
    z-index: -1;
  }


  &__content {
    margin-right: 50px;
    padding-top: 40px;
    width: 36%;
    max-width: 530px;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__swiper {
    margin: unset;
    padding: 40px 0 40px 40px;
    width: 58%;
    height: fit-content;
  }

  &__slide {
    height: 360px;
    border-radius: 7px;
    border: 1px solid #2EECC580;
    box-shadow: 0 0 40px 0 #2EECC51A;


    img {
      width: 100%;
      border-radius: 7px;
    }
  }

  &__nav {
    position: absolute;
    top: 43%;
    left: 8px;
    width: 67%;
    height: 10px;
    z-index: 10;
    display: flex;
    justify-content: space-between;
  }
}

.btn-swiper {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1.5px solid #2EECC580;
  background: linear-gradient(165.96deg, #19201F 16.33%, #323939 90%);
  transition: background 0.5s ease-in-out;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 50%;
    background: linear-gradient(165.96deg, #374141 16.33%, #1b2524 90%);
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
  }

  &::after {
    content: '';
    position: absolute;
    top: -6px;
    left: -6px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 1.5px solid #153630;
  }

  &:hover {
    &::before {
      opacity: 1;
      transition: opacity 0.2s ease-in-out;
    }
  }

  &:active {
    &::before {
      background: linear-gradient(165.96deg, #1d2a2a 16.33%, #182721 90%);
    }
  }
}

.arr {
  position: absolute;
  top: 33px;


  &::before {
    content: '';
    position: absolute;
    width: 15px;
    height: 3px;
    border-radius: 4px;
    background-color: #2C8E7A;

  }
  &::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 3px;
    border-radius: 4px;
    background-color: #2C8E7A;
  }

  &--prev {
    left: 20px;
    &::before {
      top: -9px;
      left: 0;
      transform: rotate(-45deg);
    }
    &::after {
      transform: rotate(45deg);
    }

  }
  &--next {
    right: 33px;
    &::before {
      top: -9px;
      left: 0;
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }

  }
}

@media (max-width: 1440px) {
  .portfolio {
    flex-direction: column;
    padding: 100px 0 100px 125px;

    &__content {
      margin-right: 0;
      padding-right: 125px;
      text-align: center;
      width: 100%;
      max-width: 100%;

      .link-radius {
        display: none;
      }
    }

    &__swiper {
      width: 100%;
    }
  }
}

@media (max-width: 1024px) {
  .portfolio {
    padding: 65px 0 55px 15px;

    &__content {
      padding: 0;
    }

    &__title {
      margin-bottom: 10px;
    }

    &__slide {
      height: 220px;
    }

    &__nav {
      width: 80%;
    }

  }
}

@media (max-width: 500px) {

  .portfolio {
    &::after {
      background-position-y: -170px;
      background-position-x: -255px;
      background-size: 1000px;
    }

    &__swiper {
      display: flex;
      flex-direction: column;
      padding: 0 0 100px 0;
    }

    &__slide {
      height: 150px;
    }

    .swiper-wrapper {
      margin-bottom: 35px;
    }

    &__nav {
      position: relative;
      top: unset;
      left: unset;
      justify-content: center;
      align-self: center;
    }

    &__prev {
      margin-right: 35px;
    }
  }
}
