.callback {
  position: relative;
  margin-bottom: 100px;
  height: 45vh;
  z-index: 10;
  scroll-snap-align: start;


  &__container {
    padding-top: 45px;
    padding-bottom: 59px;
    border: 1px solid #FFFFFF33;
    border-right: none;
    border-left: none;
    background: linear-gradient(91.69deg, #313134 -11.81%, #222224 23.9%, #12473C 60.63%, #292929 110.44%);
  }

  &__title {
    margin-bottom: 30px;
  }

  &__pic {
    position: absolute;
    top: -60px;
    right: 325px;
    z-index: 1;
  }
}

.form {
  display: flex;
  flex-wrap: wrap;
  &__label {
    width: 100%;
    max-width: 430px;

    &:not(:last-child) {
      margin-right: 50px;
    }

    &:nth-last-child(n+3) {
      margin-bottom: 20px;
    }

    .input-name {
      display: inline-block;
      width: 100%;
      margin-bottom: 5px;
      color: $grey75;
    }
  }

  &__input {
    padding: 0 20px;
    height: 50px;
    border-radius: 40px;
    border: 1.5px solid #FFFFFF33;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2));
    color: $white;
  }

  .btn-send {
    align-self: flex-end;
    width: 100%;
    max-width: 180px;
  }
}

.notification {
  position: absolute;
  display: flex;
  align-items: center;
  padding: 7px 10px;
  max-width: 220px;
  border-radius: 30px;
  border: 1px solid $grey20;
  background: linear-gradient(181.81deg, rgba(255, 255, 255, 0.2) 1.53%, rgba(255, 255, 255, 0) 98.47%);
  backdrop-filter: blur(20px);

  &__img {
    margin-right: 8px;
  }

  &__title {
    display: inline-block;
    margin-bottom: 3px;
    font-size: 14px;
    line-height: 19px;
    color: $white;
  }

  &__descr {
    display: inline-block;
    font-size: 12px;
    line-height: 12px;
    color: $grey75;
  }

  &--1 {
    top: 75px;
    left: -125px;
  }

  &--2 {
    top: 230px;
    right: -160px;
  }
}

@media (max-width: 1920px) {
  .callback {
    height: 80vh;
    scroll-snap-align: center;
  }
  .form {
    flex-direction: column;

    &__label {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    .btn-send {
      align-self: flex-start;
    }
  }

  .callback {
    &__pic {
      top: 60px;
      right: 270px;
    }
  }
}

@media (max-width: 1440px) {
  .callback {
    height: 90vh;
    &__pic {
      right: 50px;
    }
  }

  .notification--2 {
    top: unset;
    right: unset;
    bottom: 140px;
    left: -60px;
  }
}

@media (max-width: 1024px) {
  .callback {
    margin-bottom: 50px;
    height: unset;

    &__container {
      padding: 30px 15px;
    }

    &__title {
      text-align: center;
    }

    &__pic {
      display: none;
    }
  }

  .form {
    &__label {
      max-width: 100%;

      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 15px;
      }

      &:nth-last-child(2){
        margin-bottom: 40px;
      }
    }

    .btn-send {
      align-self: center;
    }
  }
}
