.scrollpoints {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 125px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 70px;
  z-index: 100;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 8px;
    height: 100%;
    width: 3px;
    background: linear-gradient(rgba(46, 236, 197, 0) 0%, rgba(46, 236, 197, 0.5) 43.69%, rgba(46, 236, 197, 0.0693742) 92.19%, rgba(255, 177, 177, 0) 100%);
  }
}

.scroll-block {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  transition: transform 0.2s linear;

  &:not(:last-child) {
    margin-bottom: 25px;
  }

  &--active {
    transform: translateY(-465px);
    transition: transform 0.2s linear;
  }
}

.btn-number {
  display: inline-block;
  margin-right: 10px;
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
  background: linear-gradient(91.2deg, rgba(181, 254, 239, 0.5) 10.31%, rgba(46, 236, 197, 0.5) 165.09%);
  color: transparent;
  background-clip: text;

  &--active {
    background: $font-gradient;
    color: transparent;
    background-clip: text;
  }
}

.btn-point {
  position: relative;
  width: 20px;
  height: 40px;
  border-radius: 10px;
  background: linear-gradient(180deg, #5D7C76 0%, #0A3028 100%);

  &::before {
    content: '';
    position: absolute;
    top: -3px;
    left: -3px;
    width: 26px;
    height: 46px;
    border-radius: 20px;
    border: 1.5px solid #597872;
    z-index: 1000;
  }

  &--active {
    background: linear-gradient(180deg, #B5FEEF 0%, #22A98D 100%);

    &::before {
      border: 1.5px solid #B0FBEC;
    }
  }
}

.scroll-lock {
  overflow: hidden;
}

@media (max-width: 1440px) {
  .scrollpoints {
    left: 15px;

    &::after {
      right: 6px;
    }
  }

  .btn-number {
    font-size: 18px;
  }

  .btn-point {
    width: 15px;
    height: 35px;

    &::before {
      width: 21px;
      height: 41px;
    }
  }
}

@media (max-width: 1024px) {
  .scrollpoints {
    display: none;
  }
}
