@import "./src/scss/vars";

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: Ruberoid, sans-serif;
  font-size: 17px;
  color: $main;
  background-color: $background;
  cursor: default;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: $white;
  cursor: pointer;
}

h1 {
  margin: 0;
  padding: 0;
  background : $font-gradient;
  background-clip: text;
  color: transparent;
}

h2 {
  margin: 0;
  padding: 0;
  font-size: 55px;
  line-height: 60px;
  background : $font-gradient;
  background-clip: text;
  color: transparent;
}

h3 {
  margin: 0;
  padding: 0;
  font-size: inherit;
}

h4 {
  margin: 0;
  padding: 0;
  font-size: inherit;
}

p {
  margin: 0;
  padding: 0;
  font-size: inherit;
}

figure {
  margin: 0;
  padding: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-container {
  width: 100%;
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  padding: 0;
  border: none;
  background: transparent;
  color: $white;
  cursor: pointer;
}

.input-reset {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  transition: all 0.15s ease-in-out;
}

.main {
  position: relative;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 325px;
  width: 100%;
  max-width: 1920px;
}

.link-radius {
  display: inline-block;
  padding: 15px 30px;
  border-radius: 50px;
  border: 2px solid $border;
  background-color: #2EECC51A;
  box-shadow: 0 0 62px 0 #2EECC526;
  transition: background-color 0.15s ease-in-out;

  &:hover {
    background-color: rgba(37, 141, 119, 0.6);
    transition: background-color 0.15s ease-in-out;
  }

  &:active {
    background-color: rgba(20, 91, 76, 0.6);
  }
}

.link-hover {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    bottom: -2px;
    right: 0;
    left: 0;
    margin: auto;
    width: 0%;
    height: 1px;
    background-color: rgba(70, 185, 161, 0.9);
    transition: width 0.15s ease-in-out;
  }

  &:hover {
    color: rgba(70, 185, 161, 0.9);
    transition: color 0.15s ease-in-out;

    &::after {
      width: 100%;
      transition: width 0.15s ease-in-out;
    }
  }
}


@media (min-width: 1025px ) {
  .scroll {
    height: 100vh;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    scroll-behavior: smooth;
  }

  .section {
    height: 100vh;
    scroll-snap-align: start;
  }
}

@media (max-width: 1440px) {
  .container {
    padding: 0 125px;
    max-width: 1440px;
  }

  h2 {
    font-size: 30px;
    line-height: 33px;
  }

  .page__body {
    font-size: 14px;
  }

  .container {
    padding: 0 125px;
    max-width: 1440px;
  }

  .link-radius {
    padding: 12px 20px;
    border-width: 1.5px;
  }

  .main {
    &::before {
      background-position-y: -170px;
      background-position-x: -415px;
      background-size: 2700px;
    }

    &::after {
      background-position-y: 1380px;
      background-position-x: -390px;
      background-size: 2040px;
    }
  }
}


@media (max-width: 1024px) {
  .container {
    padding: 0 15px;
    max-width: 1024px;
  }

  .main {
    &::before {
      display: none;
    }
  }
}
