.hero {
  position: relative;
  border-bottom: 5px solid transparent;
  border-image-source:linear-gradient(90deg, rgba(42, 188, 158, 0) 0%, #2ABC9E 48.65%, rgba(42, 188, 158, 0) 100%);
  border-image-slice: 1;
  overflow-x: clip;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("../img/hero.svg") no-repeat top center;
    background-size: cover;
    filter: grayscale(100%);
    z-index: -1;
  }

  &__container {
    padding-top: 200px;
  }

  &__title {
    margin-bottom: 25px;
    font-size: 65px;
    font-weight: 700;
    line-height: 65px;


    span {
      font-size: 45px;
      line-height: 62px;
      background : $font-gradient;
      color: transparent;
      background-clip: text;
    }
  }

  &__descr {
    margin-bottom: 50px;
    max-width: 45%;
  }
}

.link-more {
  margin-right: 25px;
  text-align: left;
  width: 100%;
  max-width: 200px;
  font-size: 19px;
}

.link-arrow {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 23px;
    right: 25px;
    width: 10px;
    height: 2px;
    border: 1.5px solid white;
    border-radius: 5px;
    background-color: white;
    transform: rotate(45deg);
  }
  &::after {
    content: '';
    position: absolute;
    top: 29px;
    right: 25px;
    width: 10px;
    height: 2px;
    border: 1.5px solid white;
    border-radius: 5px;
    background-color: white;
    transform: rotate(-45deg);
  }
}

.questions {
  font-size: 19px;
  color: $grey40;
}

.circles {
  position: absolute;
  right: 30px;
  bottom: 25px;
  max-width: 700px;
  max-height: 700px;
  z-index: -1;
}

.man {
  position: absolute;
  right: 140px;
  bottom: 0;
  max-width: 660px;
  max-height: 840px;
  z-index: -1;
}


@media (max-width: 1440px) {
  .hero {

    &__container {
      padding-top: 140px;
    }

    &__title {
      margin-bottom: 10px;
      font-size: 27px;
      line-height: 29px;

      span {
        font-size: 14px;
        line-height: 14px;
      }
    }

    &__descr {
      margin-bottom: 20px;
      max-width: 60%;
    }
  }

  .link-more {
    max-width: 130px;
    font-size: 12px;
  }

  .link-arrow {
    &::before {
      top: 16px;
      right: 17px;
      width: 8px;
      height: 1px;
      border: 1px solid $white;
    }

    &::after {
      top: 21px;
      right: 17px;
      width: 8px;
      height: 1px;
      border: 1px solid white;
    }
  }

  .questions {
    display: none;
  }

  .circles {
    right: -145px;
    bottom: 0;
    max-width: 550px;
    max-height: 550px;
  }

  .man {
    right: -90px;
    max-width: 530px;
    max-height: 610px;
  }

}

@media (max-width: 1024px) {

  .hero {
    height: unset;

    &__container {
      padding-bottom: 240px;
    }

    &__descr {
      font-size: 12px;
    }
  }

  .circles {
    max-width: 350px;
    max-height: 350px;
  }

  .man {
    max-width: 330px;
    max-height: 410px;
  }
}
